<template lang="html">
  <div class="df-createPoem">
    <el-form class="main c-form" ref="form" :model="form" :rules="rules">
      <el-form-item prop="title">
        <el-input
          placeholder="1-50"
          v-model="form.title"
        ></el-input>
      </el-form-item>
      <el-form-item prop="category">
        <el-autocomplete
          class="category"
          placeholder=""
          v-model="form.category"
          popper-class="c-popper"
          :fetch-suggestions="fetchCategory"
        ></el-autocomplete>
      </el-form-item>
      <el-form-item>
        <el-tag
          class="tag"
          v-for="(tag,key) in form.dynamicTags"
          :key="key"
          :closable="true"
          :close-transition="true"
          @close="closeTag(tag)"
        >{{ tag }}</el-tag>
        <el-input
          v-if="iptVisible"
          v-model="iptValue"
          ref="saveTagIpt"
          placeholder="5"
          @keyup.enter.native="confirmIpt"
          @blur="confirmIpt"
        ></el-input>
        <el-button
          v-else
          class="show-input"
          @click="showIpt"
        ></el-button>
      </el-form-item>
      <el-form-item prop="body">
        <quill-editor
          class="c-quill"
          ref="quill"
          v-model="form.body"
          :options="editorOptions"
        ></quill-editor>
      </el-form-item>
      <el-form-item>
        <el-button
        class="publish"
        :loading="isLoading"
        @click="submitForm"
        >{{ isEditPage ? '' : '' }}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import api from '@/api'
import { mapMutations, mapGetters, mapActions } from 'vuex'
import {
  quillEditor
} from 'vue-quill-editor'
export default {
  name: 'createPoem',
  components: {
    quillEditor
  },
  data() {
    return {
      isEditPage: false,  // 
      form: {
        title: '', // 
        category: '', // 
        dynamicTags: [], // 
        body: '' // 
      },
      categories: [], // 
      iptVisible: false,
      iptValue: '',
      editorOptions: { // quill 
        theme: 'bubble',
        placeholder: 'Markdown'
      },
      isLoading: false, // 
      dialogVisible: false,
      rules: { // 
        title: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 50,
            message: '1-50',
            trigger: 'blur'
          }
        ],
        category: [
          {
            required: true,
            message: '',
            trigger: 'change'
          },
          {
            validator: this.checkCategory,
            trigger: 'change'
          }
        ],
        body: [{
          required: true,
          message: '',
          trigger: 'blur'
        }]
      }
    }
  },
  computed: {
    ...mapGetters([
      'poem'
    ])
  },
  created() {
    this.checkoutPage()
  },
  methods: {
    async checkoutPage() {
      const route = this.$router.currentRoute.name
      // 
      if (route === 'editPoem') {
        this.isEditPage = true
        const poemId = this.$router.currentRoute.params.poem_id
        // 
        await this.loadPoem(`poem/${poemId}`)
        // 
        await this.fetchCategory()
        // 
        const formItems = ['title', 'dynamicTags', 'category', 'body']
        // 
        formItems.map(item => {
          if (this.poem[item]) {
            this.form[item] = this.poem[item]
            return
          }
          // 
          if (item === 'dynamicTags' && this.poem['tags'].length > 0) {
            this.poem['tags'].forEach(item => {
              this.form['dynamicTags'].push(item.name)
            })
          }
        })
      }
    },
    // 
    async fetchCategory(queryStr = '', cb) {
      await api.get(`category?query=${queryStr}`).then(response => {
        this.categories = response.data.data.map(item => {
          item['value'] = item['name']
          return item
        })
      })
      cb && cb(this.categories)
    },
    // 
    checkCategory(rule, value, cb) {
      this.categories.some(category => category['name'] === value)
        ? cb() : cb('')
    },
    // 
    closeTag(tag) {
      this.form.dynamicTags.splice(this.form.dynamicTags.indexOf(tag), 1)
    },
    // 
    showIpt() {
      this.iptVisible = true
      this.$nextTick(() => {
        this.$refs['saveTagIpt'].$refs.input.focus()
      })
    },
    // 
    confirmIpt() {
      if (this.iptValue) {
        // 
        if (this.form.dynamicTags.indexOf(this.iptValue) > -1) {
          this.$message({
            message: '',
            type: 'error',
            customClass: 'c-msg'
          })
        } else if (this.form.dynamicTags.length >= 5) {
          // 5
          this.$message({
            message: '5',
            type: 'error',
            customClass: 'c-msg'
          })
        } else {
          // 
          this.form.dynamicTags.push(this.iptValue)
        }
      }
      this.iptVisible = false
      this.iptValue = ''
    },
    // 
    submitForm() {
      this.$refs['form'].validate(async valid => {
        if (valid) {
          this.isLoading = true
          // 
          if (this.isEditPage) {
            const poemId = this.poem.id
            await api.put(`poem/${poemId}`, this.form).then(response => {
              this.isLoading = false
              if (response.data.updated) {
                this.$router.push(`/poem/${poemId}`)
                this.$message({
                  message: '',
                  type: 'success',
                  customClass: 'c-msg'
                })
              } else {
                this.$message({
                  message: '',
                  type: 'error',
                  customClass: 'c-msg'
                })
              }
            }).catch(error => {
              this.isLoading = false
              this.$message({
                message: '/(o)/~~',
                type: 'error',
                customClass: 'c-msg',
                duration: 0,
                showClose: true
              })
              Promise.reject(error)
            })
          } else {
            // 
            await api.post('poem', this.form).then(response => {
              this.isLoading = false
              // 
              if (response.data.created) {
                // 
                this.STORE_POEM(response.data.poem)
                this.$router.push(`/poem/${response.data.poem.id}`)
                this.$message({
                  message: '',
                  type: 'success',
                  customClass: 'c-msg'
                })
              } else if (response.status === 422) {
                // 
                this.$message({
                  message: response.statusText,
                  type: 'error',
                  customClass: 'c-msg'
                })
              } else {
                this.$message({
                  message: response.data.error.message,
                  type: 'error',
                  customClass: 'c-msg'
                })
              }
            }).catch(error => {
              this.isLoading = false
              this.$message({
                message: '/(o)/~~',
                type: 'error',
                customClass: 'c-msg',
                duration: 0,
                showClose: true
              })
              Promise.reject(error)
            })
          }
        }
        return false
      })
    },
    ...mapMutations([
      'STORE_POEM'
    ]),
    ...mapActions([
      'loadPoem'
    ])
  }
}
</script>

<style lang="stylus" scoped>
@import '../../common/stylus/common'

.df-createPoem
  fj(center)
  margin-top 50px
 .main
   width 50%
   .category
     width 100%
   .tag
     margin 0 20px 20px 0
   .show-input
     &::before
       margin-right 5px
       font-size 1.2em
       content '\2723'
   .publish
     width 100%
</style>



