<template lang="html">
  <div class="df-verify">
    <div class="main">
      <h1 class="title"></h1>
      <el-form class="form c-form" ref="form" :rules="rules" :model="form">
        <el-form-item label="" prop="password">
          <el-input
            placeholder="6"
            type="password"
            v-model="form.password"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="password_confirmation">
          <el-input
            placeholder=""
            type="password"
            v-model="form.password_confirmation"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            class="btn-pub publish"
            :loading="isLoading"
            :disabled="isLoaded"
            @click="submitForm"
          ></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapActions } from 'vuex'
import api from '@/api'
import c from '@/common/js'
export default {
  name: 'passwordVerify',
  data() {
    return {
      isLoaded: false,
      isLoading: false,
      form: {
        password: '',
        password_confirmation: ''
      },
      rules: {
        password: [{
          required: true,
          message: '',
          trigger: 'blur'
        }, {
          min: 6,
          max: 20,
          message: '6-20',
          trigger: 'blur'
        }, {
          type: 'string',
          pattern: /^[a-zA-Z0-9_-]{6,20}$/,
          message: '',
          trigger: 'blur'
        }],
        password_confirmation: [{
          validator: this.validatePassword,
          trigger: 'blur'
        }]
      }
    }
  },
  created() {
    c.logged()
  },
  methods: {
    // 
    validatePassword(rule, value, cb) {
      if (value === '') {
        cb(new Error(''))
      } else if (value !== this.form.password) {
        cb(new Error(''))
      } else {
        cb()
      }
    },
    submitForm() {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.isLoading = true
          const path = this.$router.currentRoute.path
          api.post(path, this.form).then(response => {
            this.isLoading = false
            if (response.data.verified) {
              this.isLoaded = true
              // 
              this.successLogin(response.data.access_token)
              this.$message({
                message: '',
                type: 'success',
                customClass: 'c-msg'
              })
            } else {
              this.$message({
                message: 'confirmation_token',
                type: 'error',
                customClass: 'c-msg',
                duration: 0,
                showClose: true
              })
            }
          }).catch(error => {
            this.isLoading = false
            this.$message({
              message: '/(o)/~~',
              type: 'error',
              customClass: 'c-msg',
              duration: 0,
              showClose: true
            })
            Promise.reject(error)
          })
        }
        return false
      })
    },
    // 
    successLogin(token) {
      //  ACCESS_TOKEN  localstorage
      window.localStorage.setItem('ACCESS_TOKEN', token)
      // 
      this.CHECKOUT_LOGIN_STATUS()
      // 
      this.loadProfile()
      // 
      this.$router.replace('/')
    },
    ...mapMutations([
      'CHECKOUT_LOGIN_STATUS'
    ]),
    ...mapActions([
      'loadProfile'
    ])
  }
}
</script>

<style lang="stylus" scoped>
@import '../../../common/stylus/common'

.df-verify
  .main
    z-index 999
    position absolute
    top 50%
    left 50%
    transform translate(-50%,-50%)
    width 25%
    .title
      text-align center
      color Green
    .form
      .publish
        width 100%
</style>



